import { WaterMetersActivityEntity } from "../../services/ports/water-meters.port"
import WaterMetersActivityItemComponent from "./water-meters-activity.item.component"

interface WaterMetersActivityListComponentProps {
    waterMeters: WaterMetersActivityEntity[]
}

function WaterMetersActivityListComponent(props: WaterMetersActivityListComponentProps) {
  const sortedwaterMeters = props.waterMeters.sort((a, b) => a.timestamp > b.timestamp ? -1 : 1)
  return (
    <>
    <h2>Ваши Показания</h2>
    {sortedwaterMeters.map((activity) => <WaterMetersActivityItemComponent key={ activity.id } waterMetersActivity={ activity    }/>)}
    </>
  )
}

export default WaterMetersActivityListComponent