import { useEffect, useState } from "react";
import { Link, NavigateFunction, useNavigate } from "react-router-dom"
import MeterListComponent from "../../components/meters-list/water-meters-list.component";
import { useAuth } from "../../hooks/useAuth";
import { IWaterMetersService, WaterMeterEntity } from "../../services/ports/water-meters.port";
import { WaterMetersService } from "../../services/water-meters/water-meters.service";

function ProfilePage() {

  const navigate: NavigateFunction = useNavigate();
  const [metersState, SetMetersState] = useState<WaterMeterEntity[]>([])
  const { isAuth } = useAuth();
  
  useEffect(() => {
    if(!isAuth) {
      navigate("/")
    }
  }, [isAuth]);
  
  useEffect(() => {
    const waterMetersService: IWaterMetersService = new WaterMetersService();
    const loadMeters = async () => {
      const meters = await waterMetersService.loadActivity();
      SetMetersState(meters);
    }

    loadMeters();
  }, []);
  
  return (
    <>
      <h2>Profile page</h2>
      <MeterListComponent waterMeters={ metersState }/>
      <br></br>
      <Link to="water-meters-activity">История показаний</Link>
    </>
  )
}

export default ProfilePage