export class WaterMetersActivityEntity {
    ownerWaterMeterId: number;
    timestamp: Date;
    meter: number;
    id: number;
    constructor (
        ownerWaterMeterId: number,
        timestamp: Date,
        meter: number,
        id: number
    ) {
        this.id = id;
        this.meter = meter;
        this.timestamp = timestamp;
        this.ownerWaterMeterId = ownerWaterMeterId;
    }
}

export class WaterMeterEntity {
    id: number;
    ownerAccountId: number;
    meters: WaterMetersActivityEntity[];
    constructor (
        id: number,
        ownerAccountId: number,
        meters: WaterMetersActivityEntity[]
    ) {
        this.id = id;
        this.ownerAccountId = ownerAccountId;
        this.meters = meters;
    }
}

export interface AddWaterMetersActivityEntity {
    ownerWaterMeterId: number,
    meter: number
}

export interface IWaterMetersService {
    addActivity(ownerMeterId: number, meter: number): Promise<WaterMetersActivityEntity>,
    loadActivity(): Promise<WaterMeterEntity[]>
}