import { useEffect, useState } from "react"
import { NavigateFunction, useNavigate } from "react-router-dom"
import WaterMetersActivityListComponent from "../../components/water-meters-activity.list/water-meters-activity.list.component"
import { useAuth } from "../../hooks/useAuth"
import { IWaterMetersService, WaterMeterEntity, WaterMetersActivityEntity } from "../../services/ports/water-meters.port"
import { WaterMetersService } from "../../services/water-meters/water-meters.service"

function WaterMetersActivityPage() {
    const navigate: NavigateFunction = useNavigate();
    const [waterMetersActivites, setWaterMetersActivites] = useState<WaterMetersActivityEntity[]>([]);
    const { isAuth } = useAuth();
    
    useEffect(() => {
        if (!isAuth) {
            navigate("/")
        }
    }, [isAuth]);

    useEffect(() => {
        const waterMetersService: IWaterMetersService = new WaterMetersService();
        const loadActivity = async () => {
            const waterMeters: WaterMeterEntity[] = await waterMetersService.loadActivity();
            const activities: WaterMetersActivityEntity[] = [];
            waterMeters.forEach((waterMeter)=> {
                activities.push(...waterMeter.meters);
            });
            setWaterMetersActivites(activities);
        }
        loadActivity();
    }, []);

  return (
    <>
    <button onClick={ () => navigate(-1) }>Назад</button>
    <WaterMetersActivityListComponent waterMeters={ waterMetersActivites }/>
    </>
    )
}

export default WaterMetersActivityPage