import { useState } from "react";
import { IWaterMetersService, WaterMeterEntity } from "../../../services/ports/water-meters.port"
import { WaterMetersService } from "../../../services/water-meters/water-meters.service";
import SubmitWaterMeterFormComponent from "../../submit-water-meters.form.component/submit-water-meter.form.component";

interface MeterItemComponentProps {
  waterMeter: WaterMeterEntity;
}

function WaterMeterItemComponent(props: MeterItemComponentProps) {

  const [isSubmit, setIsSubmit] = useState<boolean>(false);
  const [showMeters, setShowMeters] = useState<boolean>(false);

  const sortedwaterMeters = props.waterMeter.meters.sort((a, b) => a.timestamp > b.timestamp ? -1 : 1);

  const handleSubmitMeters = async (meter: number) => {
    const waterMetersService: IWaterMetersService = new WaterMetersService();
    const activity = await waterMetersService.addActivity(props.waterMeter.id, meter);
    setIsSubmit(false);
  }

  return (
    <li>
      <div>Счётчик №: {props.waterMeter.id}</div>
      <button onClick={ () => setIsSubmit(!isSubmit) }>{isSubmit ? "Закрыть форму" : "Отправить показания"}</button>
      <button onClick={ () => setShowMeters(!showMeters) }>{showMeters ? "Скрыть показания" : "Показания"}</button>
      {isSubmit && <SubmitWaterMeterFormComponent submitMeters={ handleSubmitMeters } waterMeter={ props.waterMeter }/>}
      {sortedwaterMeters.map((waterMeter) => {
        if (waterMeter.ownerWaterMeterId !== props.waterMeter.id) {
          return;
        }

      return (
        <>
      {showMeters && <div key={"values-"+waterMeter.id}>
        ID: { waterMeter.id } <br/>
        Значение: { waterMeter.meter } <br/>
        Время: { waterMeter.timestamp.toLocaleString() } <br/>
        </div>}
        </>)
      })}
      <hr/>
    </li>
  )
}

export default WaterMeterItemComponent