import { WaterMetersActivityEntity } from "../../services/ports/water-meters.port"

interface WaterMetersActivityItemComponentProps {
    waterMetersActivity: WaterMetersActivityEntity
}

function WaterMetersActivityItemComponent(props: WaterMetersActivityItemComponentProps) {
  return (
    <>
    <div>Счётчик: {props.waterMetersActivity.ownerWaterMeterId}</div>
    <div>Показание: {props.waterMetersActivity.meter}</div>
    <div>Дата: {props.waterMetersActivity.timestamp.toLocaleString()}</div>
    <hr></hr>
    </>
  )
}

export default WaterMetersActivityItemComponent