import { WaterMeterEntity } from "../../services/ports/water-meters.port";
import MeterItemComponent from "./meter-item/water-meter-item.component";

interface MeterListComponentProps {
  waterMeters: WaterMeterEntity[];
}

function WaterMetersListComponent(props: MeterListComponentProps) {
  return (
    <ul>
      {props.waterMeters.length === 0 ? "У вас нет счётчиков" : ""}
      {
        props.waterMeters.map((waterMeter: WaterMeterEntity) => <MeterItemComponent key={ "item-"+waterMeter.id } waterMeter={ waterMeter }/>)
      }
    </ul>
    )
}

export default WaterMetersListComponent