import { Dispatch } from "redux"
import { UserEntity } from "../../services/auth/user.entity"
import { userReducerActions } from "../reducers/user.reducer"

export const setUser = (user: UserEntity) => {
    return (dispatch: Dispatch) => {
        dispatch({
            type: userReducerActions.setUser,
            payload: user
        })
    }
}

export const removeUser = () => {
    return (dispath: Dispatch) => {
        dispath({
            type: userReducerActions.removeUser
        })
    }
}
