import {  AnyAction, Reducer } from "redux";
import { UserEntity } from "../../services/auth/user.entity";

export enum userReducerActions {
    setUser = "setUser",
    removeUser = "removeUser"
}

export const userReducer: Reducer = (state: UserEntity = {accountId: null, }, action: AnyAction) => {
    switch (action.type) {
        case userReducerActions.setUser:
        return action.payload;

        case userReducerActions.removeUser:
            return {accountId: null}
            
        default:
            return state;
    }
}