import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Route, Routes } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import './App.css';
import LayoutComponent from './components/layout.component/layout.component';
import AuthPage from './pages/auth/auth.page';
import ProfilePage from './pages/profile/profile.page';
import RegisterPage from './pages/register/register.page';
import WaterMetersActivityPage from './pages/water-meters-activity/water-meters-activity.page';
import { AuthService } from './services/auth/auth.service';
import { IAuthService } from './services/ports/IAuthService';
import { userActionCreators } from './state/actions-creators';

function App() {
  const dispatch = useDispatch();
  const actions = bindActionCreators(userActionCreators, dispatch)
 
  useEffect(() => {
    const initUser = async () => {
    const authService: IAuthService = new AuthService(actions);
    await authService.init();
    }
    initUser();
  }, []);

  return (
      <Routes>
        <Route path='/' element={ <LayoutComponent /> }>
          <Route index element={ <AuthPage /> } />
          <Route path='register' element={ <RegisterPage /> } />
          <Route path="profile">
            <Route index element={ <ProfilePage/> } />
            <Route path='water-meters-activity' element={ <WaterMetersActivityPage /> } />
            <Route path='*' element={ <ProfilePage/> } />
          </Route>
        </Route>
      </Routes>
  );
}

export default App;
