import { ApiService } from "../api/api.service";
import { GetAccountMetersDTO, IApiService } from "../ports/IApiService";
import { IStorage } from "../ports/IStorage";
import { AddWaterMetersActivityEntity, IWaterMetersService, WaterMeterEntity, WaterMetersActivityEntity } from "../ports/water-meters.port";
import { StorageService } from "../storage/storage.service";

export class WaterMetersService implements IWaterMetersService{
    private readonly _storage: IStorage = new StorageService();
    private readonly _apiService: IApiService = new ApiService(this._storage);
    
    constructor () {}

    async addActivity(ownerWaterMeterId: number, meter: number): Promise<WaterMetersActivityEntity> {
        const waterMetersActivity: AddWaterMetersActivityEntity = {
            ownerWaterMeterId: ownerWaterMeterId,
            meter: meter
        }
        return await this._apiService.submitMeters(waterMetersActivity);
    }

    async loadActivity(): Promise<WaterMeterEntity[]> {
        const accountMetersDto: GetAccountMetersDTO = await this._apiService.loadMeters();
        const waterMeterEntities: WaterMeterEntity[] = [];
        accountMetersDto.waterMeters.map((waterMeter) => {
            const waterMetersActivityEntities: WaterMetersActivityEntity[] = [];
            if (waterMeter.meters !== undefined) {
                waterMeter.meters.map((activity)=> {
                    return waterMetersActivityEntities.push(new WaterMetersActivityEntity(activity.ownerWaterMeterId, new Date(activity.timestamp), activity.meter, activity.id));
                });
            }
            const waterMeterEntity: WaterMeterEntity = new WaterMeterEntity(waterMeter.id, waterMeter.ownerAccountId, waterMetersActivityEntities);
            return waterMeterEntities.push(waterMeterEntity);
        });
        return waterMeterEntities;
    }
}