import { NewUserEntity } from "../auth/new-user.entity";
import { IStorage } from "../ports/IStorage";
import { GetAccountMetersDTO, IApiService, LoginResponse, RegisterResponse } from "../ports/IApiService";
import { StorageKeys } from "../ports/storage.enum";
import { ApiEndpoints, ApiMethods } from "../ports/api.enum";
import { AddWaterMetersActivityEntity, WaterMetersActivityEntity } from "../ports/water-meters.port";
import { UserEntity } from "../auth/user.entity";

export class ApiService implements IApiService {

    private _token: string | null;
    private readonly _storage: IStorage;
    private readonly _url: string = process.env.REACT_APP_API_HOT ? `${process.env.REACT_APP_API_HOST}/api/v1/` : "https://help.salavat.ru/node/api/v1/";

    constructor(storage: IStorage) {
        this._storage = storage;
        this._token = this._storage.getItem(StorageKeys.access_token);
    }

    async login(username: string, password: string): Promise<LoginResponse> {
        return await this.sendRequest<LoginResponse>(ApiEndpoints.login, ApiMethods.POST, JSON.stringify({username: username, password: password}));
    }
    
    async register(newUser: NewUserEntity): Promise<RegisterResponse> {
        return await this.sendRequest<RegisterResponse>(ApiEndpoints.register, ApiMethods.POST, JSON.stringify(newUser));
    }
    
    async checkToken(): Promise<UserEntity | null> {
        return await this.sendRequest<UserEntity | null>(ApiEndpoints.profile, ApiMethods.GET);
    }

    async submitMeters(waterMeters: AddWaterMetersActivityEntity): Promise<WaterMetersActivityEntity>{
        return await this.sendRequest<WaterMetersActivityEntity>(ApiEndpoints.waterMeters, ApiMethods.POST, JSON.stringify(waterMeters));
    }

    async loadMeters(): Promise<GetAccountMetersDTO> {
        return await this.sendRequest<GetAccountMetersDTO>(ApiEndpoints.waterMeters, ApiMethods.GET);
    }

    async sendRequest<T>(urlEndpoint: ApiEndpoints, method: ApiMethods, body?: string): Promise<T> {
        let res;
        try {
            const rawRes: Response = await fetch(this._url + urlEndpoint, {
                method: method,
                headers: {
                    'Authorization': 'Bearer ' + this._token, 
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: body
            });
            res = await rawRes.json();
        } 
        catch (e) {
            throw new Error(e as string);
        }

        return res as Promise<T>;
    }
}