import { SyntheticEvent, useState } from "react"
import { WaterMeterEntity } from "../../services/ports/water-meters.port";

interface SubmitWaterMeterFormComponentProps {
  waterMeter: WaterMeterEntity;
  submitMeters(meter: number): Promise<void>
}

function SubmitWaterMeterFormComponent(props: SubmitWaterMeterFormComponentProps) {

  const [meterState, setMeter] = useState<number>(0);

  const handleSubimt = async (e: SyntheticEvent) => {
    e.preventDefault();
    props.submitMeters(meterState);
    setMeter(0);
  }
  return (
    <div className="form-container">
      <form className="submit-meters-form" onSubmit={ handleSubimt } key={props.waterMeter.id}>
        <section>
          
        </section>
        <div>
          <label htmlFor={ "meter-"+props.waterMeter.id }>Показания счётчика</label>
          <input 
            type="number" 
            id={ "meter-"+props.waterMeter.id } 
            name={ "meter-"+props.waterMeter.id } 
            placeholder="Показание счётчика" 
            value={ meterState } 
            onChange={ e => setMeter(+e.target.value)}
          />
        </div>
        <input type="submit" value="Отправить" className="btn btn-submit"/>
      </form>
    </div>
  )
}

export default SubmitWaterMeterFormComponent